"use client";
import { useState, useMemo, useEffect } from 'react';
import { useFinance } from '@/context/FinanceContext';
import TransactionForm from '@/components/forms/TransactionForm';
import ComplexFinanceChart from '@/components/dashboard/ComplexFinanceChart';
import AccountsSidebar from '@/components/dashboard/AccountsSidebar'; // Asegúrate de importar esto y ponerlo en el JSX
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Dialog, DialogContent, DialogTrigger, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from '@/components/ui/input';
import { Label } from "@/components/ui/label";
import { PlusCircle, MinusCircle, CalendarIcon, Download, Settings, Filter } from 'lucide-react';
import { isSameMonth, isSameYear, isSameDay, format, getYear, setMonth, setYear } from 'date-fns';
import { es } from 'date-fns/locale';
import Link from 'next/link';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import { cn } from '@/lib/utils';

export default function Dashboard() {
  const { transactions, categories, accounts } = useFinance();
  const [period, setPeriod] = useState('day');
  const [selectedDate, setSelectedDate] = useState<Date>(new Date());
  const [exportStart, setExportStart] = useState('');
  const [exportEnd, setExportEnd] = useState('');
  // --- LOGICA FECHAS (PUNTO 1) ---
  const currentYear = new Date().getFullYear();
  const years = Array.from({length: 5}, (_, i) => currentYear - 2 + i); // Año actual +- 2
  const months = Array.from({length: 12}, (_, i) => i); // 0-11

  // Componente Selector de Fecha Inteligente
  const DateSelector = () => {
      if (period === 'day') {
          return (
            <Popover>
                <PopoverTrigger asChild>
                    <Button variant={"outline"} className={cn("w-[220px] justify-start text-left font-normal bg-zinc-950 border-zinc-700")}>
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {format(selectedDate, "PPP", { locale: es })}
                    </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0 bg-zinc-900 border-zinc-800" align="end">
                    <Calendar mode="single" selected={selectedDate} onSelect={(d) => d && setSelectedDate(d)} initialFocus className="p-3"/>
                </PopoverContent>
            </Popover>
          );
      } else if (period === 'month') {
          return (
              <div className="flex gap-2">
                  <Select value={selectedDate.getMonth().toString()} onValueChange={(m) => setSelectedDate(setMonth(selectedDate, parseInt(m)))}>
                      <SelectTrigger className="w-[140px] bg-zinc-950 border-zinc-700"><SelectValue /></SelectTrigger>
                      <SelectContent>
                          {months.map(m => <SelectItem key={m} value={m.toString()}>{format(new Date(2024, m, 1), 'MMMM', {locale: es})}</SelectItem>)}
                      </SelectContent>
                  </Select>
                  <Select value={getYear(selectedDate).toString()} onValueChange={(y) => setSelectedDate(setYear(selectedDate, parseInt(y)))}>
                      <SelectTrigger className="w-[100px] bg-zinc-950 border-zinc-700"><SelectValue /></SelectTrigger>
                      <SelectContent>{years.map(y => <SelectItem key={y} value={y.toString()}>{y}</SelectItem>)}</SelectContent>
                  </Select>
              </div>
          );
      } else {
          return (
             <Select value={getYear(selectedDate).toString()} onValueChange={(y) => setSelectedDate(setYear(selectedDate, parseInt(y)))}>
                 <SelectTrigger className="w-[120px] bg-zinc-950 border-zinc-700"><SelectValue /></SelectTrigger>
                 <SelectContent>{years.map(y => <SelectItem key={y} value={y.toString()}>{y}</SelectItem>)}</SelectContent>
             </Select>
          );
      }
  };

  // --- FILTRO PRINCIPAL DE DATOS ---
  const filteredTx = useMemo(() => {
    return transactions.filter(t => {
        const tDate = new Date(t.date);
        if (period === 'day') return isSameDay(tDate, selectedDate);
        if (period === 'month') return isSameMonth(tDate, selectedDate);
        if (period === 'year') return isSameYear(tDate, selectedDate);
        return true;
    }).sort((a,b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  }, [transactions, period, selectedDate]);

  // --- KPIS CON PORCENTAJES (PUNTO 3) ---
  const incomeReal = filteredTx.filter(t => t.type === 'income').reduce((acc, curr) => acc + curr.amount, 0);
  const expenseReal = filteredTx.filter(t => t.type === 'expense').reduce((acc, curr) => acc + curr.amount, 0);
  const savingsReal = filteredTx.filter(t => t.type === 'income' && t.categoryId === 'cat_sys_savings').reduce((acc, curr) => acc + curr.amount, 0);
  const remaining = incomeReal - expenseReal;

  // Porcentajes
  const expensePct = incomeReal > 0 ? (expenseReal / incomeReal) * 100 : 0;
  const savingsPct = incomeReal > 0 ? (savingsReal / incomeReal) * 100 : 0;
  const remainingPct = incomeReal > 0 ? (remaining / incomeReal) * 100 : 0;


  // --- EXPORTAR MODAL (PUNTO 2) ---
  const [exportAccounts, setExportAccounts] = useState<string[]>([]);
  const [exportType, setExportType] = useState('all');
  const handleExport = () => {
      // Filtrar
      let dataToExport = transactions.filter(t => {
           const tDate = new Date(t.date);

           // 1. Lógica de filtro de fecha
           // Si el usuario puso fechas en el modal, usamos esas.
           if (exportStart && exportEnd) {
               const start = new Date(exportStart);
               const end = new Date(exportEnd);
               // Ajustamos end al final del día para incluir movimientos de ese día
               end.setHours(23, 59, 59);
               
               if (tDate < start || tDate > end) return false;
           } else {
               // Si no puso fechas, usamos el filtro del dashboard (Día/Mes/Año actual)
               const matchDate = period === 'day' ? isSameDay(tDate, selectedDate) : 
                             period === 'month' ? isSameMonth(tDate, selectedDate) : isSameYear(tDate, selectedDate);
               if(!matchDate) return false;
           }

           // Filtro tipo (Ingreso/Gasto)
           if(exportType !== 'all' && t.type !== exportType) return false;

           // Filtro Cuentas
           if(exportAccounts.length > 0 && !exportAccounts.includes(t.accountId)) return false;

           return true;
      });

      // ... resto del código de exportación a Excel (XLSX) ...
      const ws = XLSX.utils.json_to_sheet(dataToExport.map(t => ({
          Fecha: format(new Date(t.date), 'yyyy-MM-dd'),
          Tipo: t.type === 'income' ? 'Ingreso' : 'Gasto',
          Monto: t.amount,
          Cuenta: accounts.find(a => a.id === t.accountId)?.name || 'N/A',
          Categoria: categories.find(c => c.id === t.categoryId)?.name || 'N/A',
          Detalle: t.description
      })));
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "Reporte");
      const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
      const data = new Blob([excelBuffer], {type: "application/octet-stream"});
      saveAs(data, `FinanceHome_Reporte.xlsx`);
};

  return (
    <div className="space-y-6 pb-24 relative">
       {/* Sidebar Superpuesto */}
       <AccountsSidebar />

      {/* Header */}
      <div className="flex flex-col md:flex-row justify-between items-start gap-4">
        <div className="flex items-center gap-4">
            <div>
                <h1 className="text-3xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400">
                HOME FINANCE
                </h1>
                <p className="text-sm text-zinc-400 capitalize">
                    {period === 'day' ? format(selectedDate, "EEEE, d 'de' MMMM", { locale: es }) : 
                     period === 'month' ? format(selectedDate, "MMMM yyyy", { locale: es }) : format(selectedDate, "yyyy")}
                </p>
            </div>
            <Link href="/admin">
                <Button variant="outline" size="icon" className="border-zinc-700 bg-zinc-900"><Settings className="h-5 w-5" /></Button>
            </Link>
        </div>
        
        <div className="flex gap-2 w-full md:w-auto flex-wrap">
            {/* BOTÓN EXPORTAR AVANZADO */}
            <Dialog>
                <DialogTrigger asChild>
                    <Button variant="secondary" className="flex-1 md:flex-none"><Download className="mr-2 h-4 w-4" /> Exportar</Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800">
                    <DialogTitle>Exportar Reporte</DialogTitle>
                    <div className="space-y-4 py-4">
                        {/* NUEVO: Filtros de Fecha para Exportar */}
                        <div className="grid grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <Label>Desde</Label>
                                <Input 
                                    type="date" 
                                    className="bg-black border-zinc-800" 
                                    value={exportStart}
                                    onChange={(e) => setExportStart(e.target.value)} 
                                />
                            </div>
                            <div className="space-y-2">
                                <Label>Hasta</Label>
                                <Input 
                                    type="date" 
                                    className="bg-black border-zinc-800" 
                                    value={exportEnd}
                                    onChange={(e) => setExportEnd(e.target.value)} 
                                />
                            </div>
                        </div>

                        <div className="space-y-2">
                            <Label>Tipo</Label>
                            <Select value={exportType} onValueChange={setExportType}>
                                {/* ... opciones existentes ... */}
                            </Select>
                        </div>
                        {/* ... selector de cuentas existente ... */}
                    </div>
                    <DialogFooter>
                        <Button onClick={handleExport} className="bg-emerald-600">Descargar Excel</Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
            
            <Dialog>
                <DialogTrigger asChild><Button className="flex-1 bg-emerald-600"><PlusCircle className="mr-2 h-4 w-4" /> Ingreso</Button></DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800"><DialogTitle>Nuevo Ingreso</DialogTitle><TransactionForm defaultType="income" /></DialogContent>
            </Dialog>

            <Dialog>
                <DialogTrigger asChild><Button className="flex-1 bg-rose-600"><MinusCircle className="mr-2 h-4 w-4" /> Gasto</Button></DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800"><DialogTitle>Nuevo Gasto</DialogTitle><TransactionForm defaultType="expense" /></DialogContent>
            </Dialog>
        </div>
      </div>

      {/* GRÁFICA */}
      <Card className="bg-zinc-900/50 border-zinc-800 shadow-xl">
        <CardHeader className="pb-2"><CardTitle>Flujo de Dinero</CardTitle></CardHeader>
        <CardContent>
           <ComplexFinanceChart transactions={transactions} period={period} currentDate={selectedDate} />
        </CardContent>
      </Card>

      {/* TABS Y CONTROLES */}
      <Tabs value={period} onValueChange={(v) => setPeriod(v)} className="space-y-6">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 bg-zinc-900/50 p-2 rounded-lg border border-zinc-800">
            <TabsList className="bg-zinc-950 border border-zinc-800">
                <TabsTrigger value="day">Día</TabsTrigger>
                <TabsTrigger value="month">Mes</TabsTrigger>
                <TabsTrigger value="year">Año</TabsTrigger>
            </TabsList>
            
            {/* Selector Dinámico */}
            <DateSelector />
        </div>

        <TabsContent value={period} className="space-y-6">
           {/* CARDS CON PORCENTAJES GRANDES */}
           <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
              <KPICard title="Ingresos" value={incomeReal} color="text-emerald-400" />
              <KPICard title="Gastos" value={expenseReal} color="text-rose-400" pct={expensePct} pctLabel="Gastado" />
              <KPICard title="Ahorro" value={savingsReal} color="text-blue-400" pct={savingsPct} pctLabel="Ahorrado" />
              <KPICard title="Restante" value={remaining} color="text-indigo-400" pct={remainingPct} pctLabel="Disponible" />
           </div>

           {/* Tabla */}
           <Card className="bg-zinc-900 border-zinc-800">
               <CardContent className="p-0">
                   <div className="divide-y divide-zinc-800">
                       {filteredTx.map(tx => (
                           <div key={tx.id} className="flex justify-between items-center p-4 hover:bg-zinc-950/50">
                               <div className="flex gap-3 items-center">
                                   <div className={`w-1 h-8 rounded-full ${tx.type==='income'?'bg-emerald-500':'bg-rose-500'}`}/>
                                   <div>
                                       <p className="font-bold text-zinc-200">{tx.description}</p>
                                       <p className="text-xs text-zinc-500">{format(new Date(tx.date), 'dd MMM HH:mm')} • {categories.find(c=>c.id===tx.categoryId)?.name}</p>
                                   </div>
                               </div>
                               <div className="text-right">
                                   <p className={`font-mono font-bold ${tx.type==='income'?'text-emerald-400':'text-rose-400'}`}>
                                       {tx.type==='income'?'+':'-'} ${tx.amount.toLocaleString()}
                                   </p>
                                   <p className="text-xs text-zinc-600">{accounts.find(a=>a.id===tx.accountId)?.name}</p>
                               </div>
                           </div>
                       ))}
                       {filteredTx.length === 0 && <p className="text-center text-zinc-500 py-6">No hay movimientos.</p>}
                   </div>
               </CardContent>
           </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}

function KPICard({ title, value, color, pct, pctLabel }: any) {
    return (
        <Card className="bg-zinc-900 border-zinc-800 relative overflow-hidden group">
            <CardContent className="p-5 flex flex-col justify-between h-full">
                <div className="flex justify-between items-start">
                    <div>
                        <p className="text-xs text-zinc-500 uppercase tracking-wider font-bold">{title}</p>
                        <p className={`text-2xl lg:text-3xl font-extrabold tracking-tight mt-1 ${color}`}>${value.toLocaleString()}</p>
                    </div>
                    {/* Porcentaje Grande */}
                    {pct !== undefined && (
                        <div className="text-right">
                             <p className="text-3xl font-black text-zinc-800 group-hover:text-zinc-700 transition-colors">{pct.toFixed(0)}%</p>
                        </div>
                    )}
                </div>
                {pct !== undefined && (
                    <div className="mt-4 w-full bg-zinc-800 h-1.5 rounded-full overflow-hidden">
                        <div className={`h-full ${color.replace('text-', 'bg-')}`} style={{width: `${Math.min(pct, 100)}%`}}></div>
                    </div>
                )}
            </CardContent>
        </Card>
    )
}