// components/forms/TransactionForm.tsx
"use client";
import { useState } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Save, AlertCircle } from 'lucide-react';
import { v4 as uuidv4 } from 'uuid';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface Props {
    defaultType?: 'income' | 'expense';
    onClose?: () => void;
}

export default function TransactionForm({ defaultType = 'expense', onClose }: Props) {
  const { addTransaction, addFixedRule, accounts, categories } = useFinance();
  
  const [type, setType] = useState<'income' | 'expense'>(defaultType);
  // NUEVO: Estado para diferenciar Gasto Real vs Ahorro
  const [expenseType, setExpenseType] = useState<'real' | 'savings'>('real'); 

  const [amountReal, setAmountReal] = useState('');
  const [amountBudgeted, setAmountBudgeted] = useState('');
  const [desc, setDesc] = useState('');
  const [accountId, setAccountId] = useState('');
  const [categoryId, setCategoryId] = useState('');
  const [subCategory, setSubCategory] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  
  const [isFixedBooking, setIsFixedBooking] = useState(false);
  const [dayOfMonth, setDayOfMonth] = useState('1');

  const selectedCategory = categories.find(c => c.id === categoryId);
  const canBeFixed = type === 'expense' && expenseType === 'real';

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!amountReal || !accountId || !categoryId) return;

    const finalRealAmount = parseFloat(amountReal);
    const finalBudgetedAmount = type === 'income' ? finalRealAmount : (amountBudgeted ? parseFloat(amountBudgeted) : finalRealAmount);

    // CORRECCIÓN DE FECHA: Agregamos T12:00:00 para evitar el cambio de día por zona horaria
    const safeDate = new Date(`${date}T12:00:00`).toISOString();

    addTransaction({
      amount: finalRealAmount,
      budgetedAmount: finalBudgetedAmount,
      type,
      accountId,
      categoryId,
      subCategoryId: subCategory || undefined,
      description: expenseType === 'savings' ? `[AHORRO] ${desc}` : desc ,
      date: safeDate, // Usamos la fecha segura
      isFixed: isFixedBooking,
      // Usamos el campo description o uno nuevo para marcar si fue ahorro
      // En tu lógica actual, el ahorro se detecta por categoría, pero podemos forzarlo en la descripción o un campo meta
      
    });

    if (isFixedBooking && canBeFixed) {
        addFixedRule({
            id: uuidv4(),
            description: desc || selectedCategory?.name || 'Gasto Fijo',
            categoryId,
            budgetedAmount: finalBudgetedAmount,
            dayOfMonth: parseInt(dayOfMonth),
            accountIdToCharge: accountId,
            isActive: true
        });
        alert("Regla creada");
    }
    
    resetForm();
    if (onClose) onClose();
  };

  const resetForm = () => {
    setAmountReal(''); setAmountBudgeted(''); setDesc(''); setSubCategory(''); setIsFixedBooking(false);
  }

  const filteredCategories = categories.filter(c => {
      if (type === 'income') return c.type === 'income';
      return c.type === 'expense' || c.type === 'payment';
  });

  return (
    <div className="space-y-4 text-white">
      <div className={`h-2 w-full rounded-full mb-4 ${type === 'income' ? 'bg-green-500' : 'bg-red-500'}`} />

      <form onSubmit={handleSubmit} className="space-y-4">
        
        {/* NUEVO SELECTOR: Gasto Real vs Ahorro (Solo visible si es GASTO) */}
        {type === 'expense' && (
            <div className="flex bg-zinc-800 p-1 rounded-lg mb-4">
                <button
                    type="button"
                    onClick={() => setExpenseType('real')}
                    className={`flex-1 py-1 text-sm rounded-md transition-all ${expenseType === 'real' ? 'bg-red-600 text-white shadow' : 'text-zinc-400 hover:text-white'}`}
                >
                    Gasto Real
                </button>
                <button
                    type="button"
                    onClick={() => setExpenseType('savings')}
                    className={`flex-1 py-1 text-sm rounded-md transition-all ${expenseType === 'savings' ? 'bg-blue-600 text-white shadow' : 'text-zinc-400 hover:text-white'}`}
                >
                    Es Ahorro / Inversión
                </button>
            </div>
        )}

        <div className="space-y-1">
            <Label>Fecha</Label>
            <Input type="date" value={date} onChange={e => setDate(e.target.value)} className="bg-zinc-800 border-zinc-700" />
        </div>

        <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
            <Label className={type === 'income' ? 'text-green-400' : 'text-red-400 font-bold'}>
                {type === 'income' ? 'Valor Recibido' : 'Valor Pagado'} *
            </Label>
            <Input 
                type="number" step="0.01" placeholder="$0" required
                className="text-xl font-bold bg-zinc-900 border-zinc-700 h-12"
                value={amountReal} onChange={e => setAmountReal(e.target.value)}
            />
            </div>
            
            {type === 'expense' && expenseType === 'real' && (
                <div className="space-y-2">
                <Label className="text-zinc-400">Presupuestado</Label>
                <Input 
                    type="number" step="0.01" placeholder="Opcional"
                    className="text-lg bg-zinc-800 border-zinc-700 h-12 opacity-80"
                    value={amountBudgeted} onChange={e => setAmountBudgeted(e.target.value)}
                />
                </div>
            )}
        </div>

        {/* ... Resto del formulario (Selectores de Cuenta, Categoría, etc.) se mantienen igual ... */}
        <div className="grid grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label>Cuenta</Label>
            <Select onValueChange={setAccountId} required>
              <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
              <SelectContent>
                {accounts.filter(a => !a.isHidden).map(acc => (
                  <SelectItem key={acc.id} value={acc.id}>{acc.bank} - {acc.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label>Categoría</Label>
            <Select onValueChange={setCategoryId} required>
              <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
              <SelectContent>
                {filteredCategories.map(cat => (
                  <SelectItem key={cat.id} value={cat.id}>{cat.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
        
        <div className="space-y-2">
            <Label>Descripción</Label>
            <Input className="bg-zinc-800 border-zinc-700" value={desc} onChange={e => setDesc(e.target.value)} />
        </div>

        {/* Switch de Recurrente solo si es Gasto Real */}
        {canBeFixed && (
            <div className="flex items-center justify-between p-3 bg-zinc-800/50 rounded border border-zinc-700">
               <Label className="text-zinc-300">¿Repetir mensualmente?</Label>
               <Switch checked={isFixedBooking} onCheckedChange={setIsFixedBooking} />
            </div>
        )}
        {isFixedBooking && (
             <div className="flex items-center gap-2 mt-2">
                <Label>Día del mes:</Label>
                <Input type="number" min="1" max="31" value={dayOfMonth} onChange={e => setDayOfMonth(e.target.value)} className="w-20 bg-zinc-800"/>
             </div>
        )}

        <Button type="submit" className={`w-full h-12 text-lg ${type === 'income' ? 'bg-green-600' : 'bg-red-600'}`}>
          <Save className="mr-2 h-5 w-5" /> Registrar
        </Button>
      </form>
    </div>
  );
}